body{
  background-color: rgb(215, 223, 222);
}


.cocktailName {
  padding-top: 5%;
  font-family: Montserrat;
  -webkit-margin-before: 1%;
          margin-block-start: 1%;
}

.withMainIngredient {
  font-family: Montserrat;
  -webkit-margin-before: 1%;
          margin-block-start: 1%;
  -webkit-margin-after: 5%;
          margin-block-end: 5%;
}

.nextRandom{
  font-size: 3.5vh;
  font-family: Roboto Condensed;
  color:rgb(54, 55, 58);;
  border-width: thin;
  margin-left: 2vw;
  margin-right: 2vw;
  margin-top: 3vh;
  border-radius: 10px;
}

.PicAndListWrapper {
  display: flex;
  margin: auto;
  flex-direction: row;
  margin-bottom: 4%;
  margin-top: 7%;
}

.columnPicAndListsWrapper {
  display: flex;
  flex-direction: row;
  flex-basis: 100%;
  align-items: center;
}

.picture {
  width: 80%;
  border-radius: 8px;
  margin: auto;
  margin-top: 0%;
  height: 95%;
}

.back{
  position: relative;
  left: 3vw;
  text-align: left;
  top: 4vh;
}

.fa-angle-left:before {
  -webkit-text-stroke: 3px  rgb(215, 223, 222);
  color: rgb(65, 64, 64);
}

.back i:hover{
  color:rgb(138, 138, 139);
}



.ingredientsList ul {
  display: flex;
  flex-direction: column;
  -webkit-padding-end: 12%;
          padding-inline-end: 12%;
  -webkit-padding-start: 20%;
          padding-inline-start: 20%;
}

.ingredientsList {
  background-color: white;
  display: flex;
  flex-direction: column;
  width: 80%;
  border-radius: 8px;
  margin: auto;
  margin-top: 0%;
  height: 95%;
}

.recipeList {
  background-color: white;
  display: flex;
  flex-direction: column;
  width: 80%;
  border-radius: 8px;
  margin: auto;
  margin-top: 0%;
  height: 95%;
  
}

.recipeList ol {
  -webkit-padding-end: 12%;
          padding-inline-end: 12%;
  -webkit-padding-start: 20%;
          padding-inline-start: 20%;
}

li {
  text-align: left;
  padding-left: 3%;
  padding-bottom: 6%;
  font-family: Roboto Condensed;
}

/* responsive*/
/* for phones max-width 414px --> the biggest iphones */
@media screen and (max-width: 414px) {
    .cocktailName {
        margin-top: 3%;
        margin-bottom: 2%;
        font-size: 9vw;
    }

  .PicAndListWrapper {
    display: flex;
    flex-direction: column;
    margin-bottom: 4%;
  }

  .picture {
    width: 75%;
    border-radius: 8px;
    margin-top: 8%;
    margin-bottom: 5%;
  }

  .ingredientsList {
    width: 75%;
    margin-bottom: 5%;
  }

  .recipeList {
    width: 75%;
    margin-bottom: 5%;
    margin-top: 0%;
  }
  
.ingredientsList ul {
    -webkit-padding-end: 12%;
            padding-inline-end: 12%;
    -webkit-padding-start: 20%;
            padding-inline-start: 20%;
  }

h2{
    margin-bottom: 0%;
}
}

/* for phones max-width 1024px --> for ipads*/
@media screen and (min-device-width: 415px) and (max-width: 1023px) {
    .cocktailName {
        padding-top: 10%;
        font-size: 40px;
      }

    .PicAndListWrapper {
        margin-top: 12%;
        flex-direction: column;
      }


      .withMainIngredient {
        font-size: 20px;
    }

      .picture {
        width: 50%;
        margin-bottom: 5%;
      }
    
      .ingredientsList {
        width: 50%;
        margin-bottom: 5%;
      }
    
      .recipeList {
        width: 50%;
        margin-bottom: 5%;
      }

      .h2{
          font-size: 3vh;
      }
}


/* ipad pro*/
@media screen and (min-device-width: 769px) and (max-width: 1024px) {
    
    .cocktailName {
        font-size: 40px;
    }

    .ingredientsList ul {
        font-size: 18px;
    }

    .withMainIngredient {
        font-size: 18px;
    }

    h2{
        font-size: 20px;
    }
}

